
import React from 'react';
import { conNota } from '../../Modelos/Factura';
import FilaVentas from '../../Utiles/Prototipos/FilaVentas';

export default class FilaNota extends FilaVentas {
    con=conNota();
    constructor(p: any) {
        super(p);
        this.Label = 'NOTA DE ENTREGA';
        this.tipo = 'Nota';
    }
    getStatus() {
        switch (this.props.data.status) {
            case 0:
                return <span className="badge badge-danger">CANCELADA</span>
            case 1:
                return <span className="badge badge-warning">ENTREGADO</span>
            case 2:
                return <span className="badge badge-success">FACTURADA</span>
        }
    }
    cancelado() {
        return 'NOTA DE ENTREGA N¬∞ ' + this.props.data.codigo + ' CANCELADA ESXITOSAMENTE';
    }
}