import React from "react";
import { Accordion, Button, Card } from "react-bootstrap";
import { toast } from "react-toastify";
import { VentasContext, VentasType } from "../../Context/Ventas";
import { conCotizacion, conNota, Cotizacion, Nota, sentNota } from "../../Modelos/Factura";
import { GeneralFila, generalNull } from "../../Modelos/General";
import { InputArea } from "../../Utiles/Inputs";
import ClienteSelector from "../Clientes/Selector";
import BuscarCotizacion from "../Cotizacion/BuscarCotizacion";
import AuxiliarFactura from "../Facturas/Nuevo/AuxiliarFactura";
import BuscarNota from "./BuscarNota";
interface State extends Nota {
    cargarCotizacion: boolean
    cargarNota: boolean
    dolar: boolean
}
class NuevaNota extends React.Component<any, State>{
    con = conNota();
    procesar = true;
    constructor(p: any) {
        super(p);
        this.setState = this.setState.bind(this)
        this.setCliente = this.setCliente.bind(this)
        this.state = { ...generalNull, cod_cliente: '', cargarCotizacion: false, cargarNota: false, dolar: false };
    }
    render() {
        let { state: { cargarCotizacion, cargarNota, nota, cod_cliente, codigo }, setCliente, setState } = this;
        if (codigo > 0)
            return <iframe src={"/api/Nota/PDF/" + codigo} width='100%' height='900wh' title="print" scrolling='no' frameBorder='0' />

        return <Card>
            <Card.Header className='bg-info'>
                <h4 className='mr-auto'><i className="fa fa-edit"></i> NUEVA NOTA DE ENTREGA</h4>
            </Card.Header>
            <Card.Body>
                <ClienteSelector Cliente={cod_cliente} add={setCliente} />
                <Accordion>
                    <Accordion.Toggle eventKey='0' as={Button} variant='primary'>NOTA</Accordion.Toggle>
                    <Accordion.Collapse eventKey='0'><Card>
                        <Card.Body>
                            <InputArea rows={4} maxLength={600} value={nota} setData={this.setState} update="nota" />
                        </Card.Body>
                    </Card>
                    </Accordion.Collapse>
                </Accordion>
                <BuscarCotizacion open={cargarCotizacion} add={this.cargarCotizacion.bind(this)} close={this.close.bind(this)} />
                <BuscarNota open={cargarNota} add={this.cargarNota.bind(this)} close={this.close.bind(this)} />
                <AuxiliarFactura nota={true} key="auxNota" Prosesar={this.propcesar.bind(this)} setData={setState} data={this.state} />
            </Card.Body>
        </Card>
    }
    close() { this.setState({ cargarCotizacion: false, cargarNota: false }) }
    cargarCotizacion(d: GeneralFila) {
        const { tasa } = this.context as VentasType;
        conCotizacion().detalle(d.codigo, (data: Cotizacion) => {
            let d: any = { ...data };
            console.log(d);
            d.id_cotizacion = data.codigo;
            d.codigo = 0;
            d.nota = this.state.nota;
            if (data.tasa !== 0)
                d.detalles = data.detalles.map(p => {
                    if (data.tasa > 0) {
                        p.precio = p.precio * tasa / data.tasa;
                        p.precio = parseFloat(p.precio.toFixed(2));
                    }
                    return p;
                })
            this.setState(d)
        });
    }
    cargarNota(d: GeneralFila) {
        this.con.detalle(d.codigo, (data) => {
            data.codigo = 0;
            data.nota = this.state.nota;
            this.setState(data)
        });
    }
    setCliente(p: string) {
        this.setState({ cod_cliente: p });
    }
    propcesar() {
        let procesar = true;
        const { data: { User: { nivel } }, findProducto, impuesto } = this.context as VentasType;
        if (this.state.cod_cliente === "") {
            toast('DEBE SELECCIONAR UN CLIENTE', { type: toast.TYPE.ERROR });
            procesar = false;
        } else if (this.state.detalles.length === 0) {
            toast('DEBE SELECCIONAR AL MENOS UN PRODUCTO', { type: toast.TYPE.ERROR });
            procesar = false;
        } else if (procesar)
            this.state.detalles.forEach(p => {
                const pr = findProducto(p.codigo);
                const cos = (nivel === 0) ? pr.costo : (pr.costo * (1 + pr.precio1 / 100));
                if (p.unidades > pr.cantidad) {
                    if (p.tipo !== 2) {
                        toast('NO HAY SUFICENTE ' + p.descripcion + ' PARA COMPLETAR LA OPERACION', { type: toast.TYPE.ERROR });
                        procesar = false;
                    }
                } else if (cos > p.precio) {
                    toast(p.descripcion + ' ESTA POR DEBAJO DE SU ' + ((nivel === 0) ? 'COSTO' : 'PRECIO MAS BAJO') + ' REGISTRADO', { type: toast.TYPE.ERROR });
                    procesar = false;
                }
            })
        if (this.procesar && procesar) {
            this.procesar = false;
        } else {
            toast('YA LE A DADO PROCESAR', { type: toast.TYPE.ERROR });
            procesar = false;
        }
        if (procesar)
            this.con.nuevo(sentNota(this.state, impuesto), data => { this.setState({ codigo: data }) });
    }
    componentDidMount() {
        let s = localStorage.getItem('NotaCache');
        if (s !== null) {
            let data = JSON.parse(s);
            this.setState(data);
        }
    }
    componentDidUpdate() {
        if (this.state.cargarCotizacion === this.state.cargarNota)
            localStorage.setItem('NotaCache', JSON.stringify(this.state));
    }
    componentWillUnmount() {
        localStorage.removeItem('NotaCache');
    }
}
NuevaNota.contextType = VentasContext;
export default NuevaNota;