
import React from 'react';
import { Button, ButtonGroup } from 'react-bootstrap';
import { conCotizacion } from '../../Modelos/Factura';
import ModalPrint from '../../Utiles/Componentes/ModalPrint';
import Seguimientos from '../../Utiles/Componentes/Seguimiento';
import { zfill } from '../../Utiles/Formatos';
import FilaVentas from '../../Utiles/Prototipos/FilaVentas';
import ModalDetalle from './ModalDetalle';
import PrintDolar from './ModalPrint';

export default class FilaCotizacio extends FilaVentas {
    con = conCotizacion();
    constructor(p: any) {
        super(p);
        this.Label = 'COTIZACI√ìN';
        this.tipo = 'Cotizacion';
    }
    getModal() {
        let { codigo, tasa } = this.props.data;
        return <>
            {(tasa > 0) ? <PrintDolar title={"IMPRIMIR " + this.Label + " N¬∞ " + zfill(codigo)}
                codigo={zfill(codigo)}
                key={'PrintDolar' + this.tipo + codigo}
                open={this.state.print} close={this.close.bind(this)} /> : <ModalPrint
                key={'ModalPrint' + this.tipo + codigo}
                title={"IMPRIMIR " + this.Label + " N¬∞ " + zfill(codigo)}
                url={"/api/" + this.tipo + "/PDF/" + codigo}
                open={this.state.print} close={this.close.bind(this)} />}
            <Seguimientos
                key={'Seguimientos' + this.tipo + codigo}
                type='Cotizacion'
                id={codigo}
                close={this.close.bind(this)}
                open={this.state.aux}
            />
            <ModalDetalle
                key={'ModalDetalle' + this.tipo + codigo}
                titulo={"DETALLES DE LA COTIZACI√ìN N¬∞ " + zfill(codigo)}
                id={codigo}
                open={this.state.detalles}
                close={this.close.bind(this)} />
        </>
    }
    getStatus() {
        switch (this.props.data.status) {
            case 1:
                return <span className="badge badge-warning">PENDIENTE</span>
            case 2:
                return <span className="badge badge-success">PROCESADA</span>
            default:
                return <span className="badge badge-danger">CANCELADA</span>
        }
    }
    getButtons() {
        return <ButtonGroup className="float-right">
            <Button data-tip="VER SEGUIMIENTO" variant="outline-dark"
                onClick={() => { this.setState({ aux: true }) }} >
                <i className="fa fa-eye"></i>
            </Button>
            <Button data-tip="VER IMPRECION" variant="primary"
                onClick={() => { this.setState({ print: true }) }}>
                <i className="fa fa-download"></i>
            </Button>
            {(this.props.data.status === 1) ?
                <Button data-tip="CANCELAR" variant="danger"
                    onClick={() => { this.setState({ eliminar: true }) }} >
                    <i className="fa fa-ban"></i>
                </Button> : <></>}
        </ButtonGroup>
    }
    cancelado() {
        return 'COTIZACION N¬∞ ' + this.props.data.codigo + ' CANCELADA ESXITOSAMENTE';
    }
}