
import React from 'react'
import { Button, ButtonGroup } from 'react-bootstrap'
import { Detalles } from '../../../Modelos/General'
import { InputNumber } from '../../../Utiles/Inputs'
import { ControlText } from '../../../Utiles/FormControl'
import { numberFormat } from '../../../Utiles/Formatos'
import THFila, { THInput } from '../../../Utiles/Componentes/THFila'

interface Props {
    producto: Detalles,
    edit: (iten: Detalles) => void,
    remove: (iten: Detalles) => void
}
interface State extends Detalles {
    monto_1: number,
    monto_2: number,
    monto_3: number
    open: boolean
}
export default class FilaUtilidad extends React.Component<Props, State>{
    constructor(p: Props) {
        super(p)
        this.state = { ...p.producto, monto_1: 1, monto_2: 1, monto_3: 1, open: false }
        this.setState = this.setState.bind(this)
        this.setPrecio = this.setPrecio.bind(this)
        this.setUtilidad = this.setUtilidad.bind(this)
    }
    render() {
        let {
            state: { codigo, descripcion, precio1, precio2, precio3, open, comentario, costo },
            setPrecio, setUtilidad, } = this;
        return <>
            <tr>
                <THFila width='90px'>{codigo}</THFila>
                <THFila width='auto'>{descripcion}</THFila>
                <THInput width='180px'><InputNumber min={.01} step='0.01' setData={setUtilidad} value={precio1} update="precio1" /></THInput>
                <THInput width='180px'><InputNumber min={.01} step='0.01' setData={setUtilidad} value={precio2} update="precio2" /></THInput>
                <THInput width='180px'><InputNumber min={.01} step='0.01' setData={setUtilidad} value={precio3} update="precio3" /></THInput>
                <THInput width='110px'>
                    <ButtonGroup>
                        <Button variant="info" onClick={() => { this.setState({ open: !this.state.open }) }} data-tip="MOSTRAR MAS">
                            <span className="fa fa-edit"></span>
                        </Button>
                        <Button variant="danger" onClick={() => { this.props.remove(this.state) }} data-tip="QUITAR PRODUCTO">
                            <i className="fa fa-ban"></i>
                        </Button>
                    </ButtonGroup>
                </THInput>
            </tr>
            <tr style={{ display: open ? '' : 'none' }}>
                <td colSpan={2}>
                    <ControlText setData={() => { }} before='COSTO' style={{ textAlign: 'right' }} disabled value={numberFormat(costo)} />
                </td>
                <THInput width='180px'><InputNumber min={.01} step='0.01' setData={setPrecio} value={this.getPrecio(precio1)} update="monto_1" /></THInput>
                <THInput width='180px'><InputNumber min={.01} step='0.01' setData={setPrecio} value={this.getPrecio(precio2)} update="monto_2" /></THInput>
                <THInput width='180px'><InputNumber min={.01} step='0.01' setData={setPrecio} value={this.getPrecio(precio3)} update="monto_3" /></THInput>
            </tr>
            <tr style={{ display: open ? '' : 'none' }}>
                <td colSpan={7}>
                    <ControlText maxLength={250} before="NOTA INTERNA" setData={this.setState} value={comentario} update="comentario" />
                </td>
            </tr>
        </>
    }
    getPrecio(price: number) {
        let utilidad = (price / 100) + 1
        return this.state.costo * utilidad;
    }
    setPrecio(d: { monto_1?: number, monto_2?: number, monto_3?: number }) {
        if (d.monto_1)
            this.setState({ monto_1: d.monto_1, precio1: this.getUtilidad(d.monto_1) });
        if (d.monto_2)
            this.setState({ monto_2: d.monto_2, precio2: this.getUtilidad(d.monto_2) });
        if (d.monto_3)
            this.setState({ monto_3: d.monto_3, precio3: this.getUtilidad(d.monto_3) });
    }
    getUtilidad(price: number) {
        let utilidad = ((price / this.state.costo) - 1) * 100
        return parseFloat(utilidad.toFixed(2));
    }
    setUtilidad(d: { precio1?: number, precio2?: number, precio3?: number }) {
        if (d.precio1)
            this.setState({ monto_1: this.getPrecio(d.precio1), precio1: d.precio1 });
        if (d.precio2)
            this.setState({ monto_2: this.getPrecio(d.precio2), precio2: d.precio2 });
        if (d.precio3)
            this.setState({ monto_3: this.getPrecio(d.precio3), precio3: d.precio3 });
    }
    componentWillUpdate(p: Props, s: State) {
        if (s.precio1 !== this.state.precio1 || s.precio2 !== this.state.precio2 || s.precio3 !== this.state.precio3 || s.comentario !== this.state.comentario)
            this.props.edit(s)
    }

}