import React from "react";
import { Col, Card, Accordion, Button, Row } from "react-bootstrap";
import { toast } from "react-toastify";
import { ComprasContext, ComprasType } from "../../../Context/Compras";
import { Compra, compraNull, conCompra, sentCompra } from "../../../Modelos/Compra";
import { GeneralFila } from "../../../Modelos/General";
import { ControlText } from "../../../Utiles/FormControl";
import { InputArea } from "../../../Utiles/Inputs";
import BuscarOrden from "../../Orden/BuscarOrden";
import ProveedorSelector from "../../proveedores/Selector";
import AuxiliarCompra from "./AuxiliarCompra";
interface State extends Compra {
    cargar: boolean
}
class NuevaCompra extends React.Component<any, State>{
    con = conCompra();
    procesar = true;
    constructor(p: any) {
        super(p);
        this.setState = this.setState.bind(this)
        this.state = { ...compraNull, cargar: false };
    }
    render() {
        let { state } = this;
        if (state.codigo > 0)
            return <iframe src={"/api/Compra/PDF/" + state.codigo} width='100%' height='900wh' title="print" scrolling='no' frameBorder='0' />
        return <Card>
            <Card.Header className='bg-info'>
                <h4 className='mr-auto'><i className="fa fa-edit"></i> NUEVA COMPRA</h4>
            </Card.Header>
            <Card.Body>
                <ProveedorSelector Proveedor={state.cod_proveedor} add={this.setProveedor.bind(this)} />
                <Accordion>
                    <Accordion.Toggle eventKey='0' as={Button} variant='primary'>DATOS OPCIONALES</Accordion.Toggle>
                    <Accordion.Collapse eventKey='0'>
                        <Card>
                            <Card.Body>
                                <Row>
                                    <Col data-tip="NUMERO DE FACTURA" xs={4}>
                                        <ControlText maxLength={10} before="N¬∞ FACTURA" value={state.cod_documento} setData={this.setState} update="cod_documento" />
                                    </Col>
                                    <Col data-tip="NUMERO DE CONTROL" xs={4}>
                                        <ControlText maxLength={10} before="N¬∞ CONTROL" value={state.nun_control} setData={this.setState} update="nun_control" />
                                    </Col>
                                    <Col data-tip="FECHA DE LA FACTURA" xs={4}>
                                        <ControlText type='date' before="FECHA" value={state.fecha_documento} setData={this.setState} update="fecha_documento" />
                                    </Col>
                                    <Col xs={12}><br />
                                        <b>NOTA</b>
                                        <InputArea name='nota' rows={4} maxLength={600} value={state.nota} setData={this.setState} update="nota" />
                                    </Col>
                                </Row>
                            </Card.Body>
                        </Card>
                    </Accordion.Collapse>
                </Accordion>
                <BuscarOrden open={state.cargar} add={this.cargar.bind(this)} close={() => { this.setState({ cargar: false }) }} />
                <AuxiliarCompra key="auxCompra" ordenes='CARGAR' Prosesar={this.propcesar.bind(this)} setData={this.setState} data={state} />
            </Card.Body>
        </Card>
    }
    cargar(d: GeneralFila) {
        this.con.get('api/Orden/detalles/' + d.codigo, data => {
            data.id_orden = data.codigo;
            data.codigo = 0;
            this.setState(data)
        })
    }
    setProveedor(p: string) {
        this.setState({ cod_proveedor: p });
    }
    propcesar() {
        let procesar = true;
        const { impuesto } = this.context as ComprasType;
        if (this.state.cod_proveedor === "") {
            toast('DEBE SELECCIONAR UN CLIENTE', { type: toast.TYPE.ERROR });
            procesar = false;
        } else if (this.state.detalles.length === 0) {
            toast('DEBE SELECCIONAR AL MENOS UN PRODUCTO', { type: toast.TYPE.ERROR });
            procesar = false;
        }
        if (this.procesar && procesar) {
            this.procesar = false;
        } else {
            toast('YA LE A DADO PROCESAR', { type: toast.TYPE.ERROR });
            procesar = false;
        }
        if (procesar)
            this.con.nuevo(sentCompra(this.state, impuesto), data => { this.setState({ codigo: data }) });
    }
    componentDidMount() {
        let s = localStorage.getItem('CompraCache');
        if (s !== null) {
            let data = JSON.parse(s);
            this.setState(data);
        }
    }
    componentDidUpdate() {
        if (!this.state.cargar)
            localStorage.setItem('CompraCache', JSON.stringify(this.state));
    }
    componentWillUnmount() {
        localStorage.removeItem('CompraCache');
    }
}
NuevaCompra.contextType = ComprasContext;
export default NuevaCompra;