import React from 'react'
import { Button, Col } from 'react-bootstrap';
import { Detalles } from '../../../Modelos/General';
import AuxiliarNuevo from '../../../Utiles/Prototipos/AuxiliarNuevo'
import FilaProducto from './FilaProducto';


export default class AuxiliarCompra extends AuxiliarNuevo<{ ordenes: 'COPIAR' | 'CARGAR' }> {
    nuevo = true;
    BotonesLeft() { 
        const { setData, ordenes } = this.props;
        return <Col xs={6}>
            <Button onClick={() => { setData({ cargar: true }) }}
                data-class="toolbody" data-html="true"
                data-tip={ordenes + " ORDEN <br> DE COMPRA"} variant="primary">
                <span className="fa fa-folder-open" />
            </Button>
        </Col>
    }
    Row({ data }: { data: Detalles }) {
        return <FilaProducto key={'USD' + data.codigo} producto={data} edit={this.addProductos.bind(this)} remove={this.removeProductos.bind(this)} />;
    }
}