import React from 'react'
import { Button, Modal, Row, Table } from 'react-bootstrap';
import { conCotizacion, Cotizacion } from '../../Modelos/Factura';
import { condicionesNull } from '../../Modelos/General';
import { numberFormat } from '../../Utiles/Formatos';
interface Props {
    id: number
    open: boolean,
    close: () => void,
    titulo: string
}
export default class ModalDetalle extends React.Component<Props, Cotizacion> {
    con = conCotizacion();
    state = { ...condicionesNull, cod_cliente: '', plantilla: 0, tasa: 1, dolar: false };
    render() {
        let { detalles, tasa, dolar } = this.state;
        let suma = 0;
        if (detalles === undefined)
            return <></>
        return <Modal size='xl' centered dialogClassName="modal-detalle" animation show={this.props.open} onHide={this.props.close}>
            <Modal.Header closeButton>
                <h6 className="m-0 font-weight-bold text-primary mr-auto align-self-center">
                    <span><b>{this.props.titulo}</b></span>
                </h6>
            </Modal.Header>
            <Modal.Body>
                <Row>
                    {dolar ? <Button data-tip="CAMBIAR A BOLIVARES" style={{ marginRight: "15px" }} variant="warning"
                        className="btn-block" onClick={() => { this.setState({ dolar: !dolar }) }} >
                        <span className="fa fa-usd" /> VISTA EN DOLARES
                    </Button> : <Button data-tip="CAMBIAR A DOLARES" style={{ marginRight: "15px" }} variant="primary"
                        className="btn-block" onClick={() => { this.setState({ dolar: !dolar }) }} >
                        <b>BS</b> VISTA EN BOLIVARES
                    </Button>}
                    <Table>
                        <thead>
                            <tr>
                                <th style={{ width: "100px" }}>COD</th>
                                <th>DESCRIPCION</th>
                                <th style={{ width: "40px", textAlign: "right" }}>CANT</th>
                                <th style={{ width: "80px", textAlign: "right" }}>P.U.</th>
                                <th style={{ width: "150px", textAlign: "right" }}>TOTAL</th>
                                <th>NOTA</th>
                            </tr>
                        </thead>
                        <tbody>
                            {detalles.map((s) => {
                                let { precio, unidades } = s;
                                if (dolar) precio = precio / tasa
                                suma += precio * unidades
                                return <tr>
                                    <td style={{ width: "100px" }}>{s.codigo}</td>
                                    <td>{s.descripcion}</td>
                                    <td style={{ width: "40px" }}>{numberFormat(unidades)}</td>
                                    <td style={{ width: "80px", textAlign: "right" }}>{numberFormat(precio)}</td>
                                    <td style={{ width: "150px", textAlign: "right" }}>{numberFormat(precio * unidades)}</td>
                                    <td>{s.comentario}</td>
                                </tr>
                            })}
                            <tr>
                                <td colSpan={3}></td>
                                <td style={{ width: "80px", textAlign: "right" }}>total:</td>
                                <td style={{ width: "150px", textAlign: "right" }}>{numberFormat(suma)}</td>
                            </tr>
                        </tbody>
                    </Table>
                </Row>
            </Modal.Body>
        </Modal>
    }
    dialog() {
        return <Modal.Dialog style={{ width: "90%" }} />
    }
    componentDidMount() {
        this.set();
    }
    set() {
        this.con.detalle(this.props.id, this.setState.bind(this));
    }
}