import React from 'react'
import { Button, Modal, Row, Col } from 'react-bootstrap'
import { conProducto, Producto, ProductoNull } from '../../Modelos/Productos'
import DepartamentoSelector from '../Departamento/Selector'
import UnidadesSelector from '../Unidad/Selector'
import TipoSelector from '../Tipos/Selector'
import { toast } from 'react-toastify'
import { ConConfig } from '../../Modelos/Config'
import { InputArea } from '../../Utiles/Inputs'
import { ControlNumber } from '../../Utiles/FormControl'
interface Props {
    open: boolean
    close: () => void
    add: (p: Producto) => void
}
interface State extends Producto {
    monto_1: number,
    monto_2: number,
    monto_3: number
}
export default class FormProductos extends React.Component<Props, State>{
    con = conProducto();
    tasa: number = 0;
    constructor(p: Props) {
        super(p)
        this.state = { ...ProductoNull, monto_1: 1, monto_2: 1, monto_3: 1 };
        this.setState = this.setState.bind(this)
        this.setPrecio = this.setPrecio.bind(this)
        this.setUtilidad = this.setUtilidad.bind(this)
    }
    setDepartamento(departamento: string) {
        this.setState({ departamento })
    }

    setUnidad(unidad: number) {
        this.setState({ unidad })
    }

    setTipo(tipo: number) {
        this.setState({ tipo })
    }
    render() {
        let { setState, setPrecio, setUtilidad,
            state: { exento, descripcion, precio1, precio2, precio3, costo, tipo, unidad, departamento, enser },
            props: { open, close } } = this;
        return <Modal size="xl" animation show={open} onHide={close}>
            <Modal.Header closeButton>
                <h4 className='mr-auto'><i className="fa fa-edit"></i> NUEVO PRODUCTO</h4>
            </Modal.Header>
            <Modal.Body>
                <Row>
                    <Col xs={12}>
                        <DepartamentoSelector defaultValue={departamento} change={this.setDepartamento.bind(this)} />
                    </Col>
                    <Col xs={12}><br />
                        <b>DESCRIPCI√ìN</b>
                        <InputArea rows={3} maxLength={300} value={descripcion} setData={setState} update="descripcion" />
                    </Col>
                    <Col md={4}><br />
                        <Button block onClick={() => { this.setState({ enser: enser === 1 ? 0 : 1 }) }} variant={enser === 0 ? 'success' : 'danger'}>
                            PARA LA VENTA: {enser === 0 ? 'SI' : 'NO (ENSER)'}
                        </Button>
                    </Col>
                    <Col xs={4} style={{ height: 54 }}><br />
                        <UnidadesSelector defaultValue={unidad} change={this.setUnidad.bind(this)} />
                    </Col>
                    <Col xs={4}><br />
                        <TipoSelector defaultValue={tipo} change={this.setTipo.bind(this)} />
                    </Col>
                    <Col md={4}><br />
                        <Button block onClick={() => { this.setState({ exento: !exento }) }} variant={exento ? 'success' : 'primary'}>
                            EXENTO: {exento ? 'SI' : 'NO'}
                        </Button>
                    </Col>
                    <Col xs={8} style={{ height: 54 }}><br />
                        <ControlNumber min={.01} step=".01" before='COSTO BS.' update="costo" value={costo} setData={setState} />
                    </Col>
                    <Col xs={4}><br />
                        <ControlNumber min={.01} step=".01" after="%" before="UTILIDAD 1" value={precio1} setData={setUtilidad} update="precio1" />
                    </Col>
                    <Col xs={4}><br />
                        <ControlNumber min={.01} step=".01" after="%" before="UTILIDAD 2" value={precio2} setData={setUtilidad} update="precio2" />
                    </Col>
                    <Col xs={4}><br />
                        <ControlNumber min={.01} step=".01" after="%" before="UTILIDAD 3" value={precio3} setData={setUtilidad} update="precio3" />
                    </Col>
                    <Col xs={4}><br />
                        <ControlNumber min={.01} step=".01" before="PRECIO 1" value={this.getPrecio(precio1)} setData={setPrecio} update="monto_1" />
                    </Col>
                    <Col xs={4}><br />
                        <ControlNumber min={.01} step=".01" before="PRECIO 2" value={this.getPrecio(precio2)} setData={setPrecio} update="monto_2" />
                    </Col>
                    <Col xs={4}><br />
                        <ControlNumber min={.01} step=".01" before="PRECIO 3" value={this.getPrecio(precio3)} setData={setPrecio} update="monto_3" />
                    </Col>
                </Row>
            </Modal.Body>
            <Modal.Footer>
                <Button onClick={this.create.bind(this)} variant="primary">
                    <span className="fa fa-floppy-disk" /> Guardar
                </Button>
            </Modal.Footer>
        </Modal>
    }
    getPrecio(price: number) {
        let utilidad = (price / 100) + 1
        return this.state.costo * utilidad;
    }
    setPrecio(d: { monto_1?: number, monto_2?: number, monto_3?: number }) {
        if (d.monto_1)
            this.setState({ monto_1: d.monto_1, precio1: this.getUtilidad(d.monto_1) });
        if (d.monto_2)
            this.setState({ monto_2: d.monto_2, precio2: this.getUtilidad(d.monto_2) });
        if (d.monto_3)
            this.setState({ monto_3: d.monto_3, precio3: this.getUtilidad(d.monto_3) });
    }
    getUtilidad(price: number) {
        let utilidad = ((price / this.state.costo) - 1) * 100
        return parseFloat(utilidad.toFixed(2));
    }
    setUtilidad(d: { precio1?: number, precio2?: number, precio3?: number }) {
        if (d.precio1)
            this.setState({ monto_1: this.getPrecio(d.precio1), precio1: d.precio1 });
        if (d.precio2)
            this.setState({ monto_2: this.getPrecio(d.precio2), precio2: d.precio2 });
        if (d.precio3)
            this.setState({ monto_3: this.getPrecio(d.precio3), precio3: d.precio3 });
    }
    create() {
        let { descripcion, tipo, unidad, departamento } = this.state;
        if (descripcion === '') {
            toast('DEBE COLOCAR UNA DESCRIPCION', { type: toast.TYPE.ERROR })
        } else if (departamento === 'TODO') {
            toast('DEBA SELECCIONAR UN DEPARTAMENTO', { type: toast.TYPE.ERROR })
        } else if (tipo === -1) {
            toast('DEBA SELECCIONAR UN TIPO DE PRODUCTO', { type: toast.TYPE.ERROR })
        } else if (unidad === -1) {
            toast('DEBA SELECCIONAR UNA UNIDAD DE MEDIDA', { type: toast.TYPE.ERROR })
        } else {
            this.con.nuevo(this.state, data => {
                this.props.add(data);
                this.props.close();
            })
        }
    }
    componentDidMount() {
        this.con.get('api/Dolares/valor', data => { this.tasa = data; });
        ConConfig('utilidades').pull(this.setUtilidad);
    }


}
