import React from "react";
import { Accordion, Button, Card, Row } from "react-bootstrap";
import { toast } from "react-toastify";
import ReactTooltip from "react-tooltip";
import { VentasContext, VentasType } from "../../../Context/Ventas";
import { conCotizacion, Cotizacion, sentCotizacion } from "../../../Modelos/Factura";
import { condicionesNull, GeneralFila } from "../../../Modelos/General";
import Condiciones from "../../../Utiles/Componentes/Condiciones";
import { numberFormat } from "../../../Utiles/Formatos";
import ClienteSelector from "../../Clientes/Selector";
import BuscarCotizacion from "../BuscarCotizacion";
import BuscarGuardado from "../BuscarGuardado";
import AuxiliarCotizacion from "./AuxiliarCotizacion";
interface State extends Cotizacion {
    cargar: boolean
    copiar: boolean
}
class NuevaCotizacion extends React.Component<any, State>{
    con = conCotizacion();
    procesar = true;
    tasa = true;
    constructor(p: any) {
        super(p);
        this.setCliente = this.setCliente.bind(this)
        this.copiar = this.copiar.bind(this)
        this.cargar = this.cargar.bind(this)
        this.propcesar = this.propcesar.bind(this)
        this.guardar = this.guardar.bind(this)
        this.setState = this.setState.bind(this)
        this.state = { ...condicionesNull, cod_cliente: '', plantilla: 0, tasa: 0, dolar: false, copiar: false, cargar: false };
    }
    render() {
        this.dolar();
        const { state, copiar, cargar, propcesar, guardar, setCliente, setState } = this;
        const { tasa, actConfig, } = this.context as VentasType;
        if (state.codigo > 0)
            return <Card id="tabla">
                <ReactTooltip />
                {state.dolar ? <Button data-tip="CAMBIAR A BOLIVARES" style={{ marginRight: "15px" }} variant="warning"
                    className="btn-block" onClick={() => { setState({ dolar: !state.dolar }) }} >
                    <span className="fa fa-usd" /> VISTA EN DOLARES
                </Button> : <Button data-tip="CAMBIAR A DOLARES" style={{ marginRight: "15px" }} variant="primary"
                    className="btn-block" onClick={() => { setState({ dolar: !state.dolar }) }} >
                    <b>BS</b> VISTA EN BOLIVARES
                </Button>}
                {state.dolar ? <iframe src={"/api/Cotizacion/PDFD/" + state.codigo} width='100%' height='900wh' title="print" scrolling='no' frameBorder='0' /> :
                    <iframe src={"/api/Cotizacion/PDF/" + state.codigo} width='100%' height='900wh' title="print" scrolling='no' frameBorder='0' />}
            </Card>
        return <Card>
            <Card.Header className='bg-info'>
                <h4 className='mr-auto'><i className="fa fa-edit"></i> NUEVA COTIZACION</h4>
            </Card.Header>
            <Card.Body>
                <ClienteSelector Cliente={state.cod_cliente} add={setCliente} />
                <Accordion>
                    <Accordion.Toggle eventKey='0' as={Button} variant='primary'>DATOS OPCIONALES</Accordion.Toggle>
                    <Button data-tip='ACTUALIZAR VALOR DEL DOLAR' onClick={actConfig} variant='info' className='float-right'><i className="fa fa-refresh"></i></Button>
                    <div className='float-right mr-3 mt-1'><b><span>VALOR DEL DOLAR:</span> {numberFormat(tasa)}</b> </div>
                    <Accordion.Collapse eventKey='0'><Card>
                        <Card.Body>
                            <Row>
                                <Condiciones data={state} setData={setState} />
                            </Row>
                        </Card.Body>
                    </Card>
                    </Accordion.Collapse>
                </Accordion>
                <BuscarCotizacion open={state.copiar} add={copiar} close={() => { setState({ copiar: false }) }} />
                <BuscarGuardado open={state.cargar} add={cargar} close={() => { setState({ cargar: false }) }} />
                <AuxiliarCotizacion key="auxCotizacion" guardar={guardar} Prosesar={propcesar} setData={setState} data={state} />
            </Card.Body>
        </Card>
    }
    dolar() {
        const { tasa } = this.context as VentasType;
        if ((tasa !== 0) && this.tasa) {
            this.setState({ dolar: this.context.dolar });
            this.tasa = false;
        }
    }
    copiar(d: GeneralFila) {
        this.con.detalle(d.codigo, data => {
            data.codigo = 0;
            data.tasa = this.state.tasa;
            this.setState(data)
        });
    }
    cargar(d: GeneralFila) {
        this.con.get('api/Cotizacion/cargar/' + d.codigo, data => {
            data.plantilla = data.codigo;
            data.tasa = this.state.tasa;
            data.codigo = 0;
            this.setState(data)
        })
    }
    guardar() {
        this.con.post('/api/Cotizacion/guardar', sentCotizacion(this.state, this.context.impuesto), data => {
            this.setState({ plantilla: data });
            toast("DATOS GUARDADOS EN N¬∞ " + data, { type: toast.TYPE.SUCCESS })
        }, error => {
            toast("ERROR AL CONECTAR CON SEL SERVIDOR", { type: toast.TYPE.ERROR })
        });
    }
    setCliente(p: string) {
        this.setState({ cod_cliente: p });
    }
    propcesar() {
        let procesar = true;
        if (this.state.cod_cliente === "") {
            toast('DEBE SELECCIONAR UN CLIENTE', { type: toast.TYPE.ERROR });
            procesar = false;
        } else if (this.state.detalles.length === 0) {
            toast('DEBE SELECCIONAR AL MENOS UN PRODUCTO', { type: toast.TYPE.ERROR });
            procesar = false;
        }
        if (this.procesar && procesar) {
            this.procesar = false;
        } else {
            toast('YA LE A DADO PROCESAR', { type: toast.TYPE.ERROR });
            procesar = false;
        }
        if (procesar)
            this.con.nuevo(sentCotizacion(this.state, this.context.impuesto), data => {
                this.setState({ codigo: data })
            });
    }
    componentDidMount() {
        let s = localStorage.getItem('CotizacionCache');
        if (s !== null) {
            let data = JSON.parse(s);
            this.setState(data);
        }
    }
    componentDidUpdate() {
        if (this.state.cargar === this.state.copiar)
            localStorage.setItem('CotizacionCache', JSON.stringify(this.state));
    }
    componentWillUnmount() {
        localStorage.removeItem('CotizacionCache');
    }
}
NuevaCotizacion.contextType = VentasContext;
export default NuevaCotizacion;