
import React from 'react';
import { Button, FormControl } from 'react-bootstrap';
import { Detalles } from '../../../Modelos/General';
import THFila, { THInput } from '../../../Utiles/Componentes/THFila';
import { numberFormat } from '../../../Utiles/Formatos';
import { InputNumber } from '../../../Utiles/Inputs';

interface Props {
    producto: Detalles,
    edit: (iten: Detalles) => void,
    remove: (iten: Detalles) => void
}
interface State extends Detalles { }
export default class FilaProducto extends React.Component<Props, State>{
    constructor(p: Props) {
        super(p)
        this.state = { ...p.producto }
        this.setState = this.setState.bind(this)
    }
    render() {
        let { unidades, precio, descripcion, codigo } = this.state
        return <tr>
            <THFila width='90px'>{codigo}</THFila>
            <THFila width='auto'>{descripcion}</THFila>
            <THInput width='120px'><InputNumber min={.01} step='0.01' setData={this.setState} value={unidades} update="unidades" /></THInput>
            <THInput width='180px'><InputNumber min={.01} step='0.01' setData={this.setState} value={precio} update="precio" /></THInput>
            <THInput width='180px'><FormControl disabled value={numberFormat(precio * unidades)} /></THInput>
            <THInput width='70px'>
                <Button variant="danger" onClick={() => { this.props.remove(this.state) }} data-tip="QUITAR PRODUCTO">
                    <i className="fa fa-ban"></i>
                </Button>
            </THInput>
        </tr>
    }
    componentWillUpdate(p: Props, s: State) {
        if (s.precio !== this.state.precio || s.unidades !== this.state.unidades || s.comentario !== this.state.comentario)
            this.props.edit(s)
    }

}