import React from 'react';
import { Button, Modal, Row } from 'react-bootstrap';
import ReactTooltip from 'react-tooltip';
interface Props {
    open: boolean,
    close: () => void,
    codigo: string,
    title: string
}
export default class ModalPrint extends React.Component<Props, { dolar: boolean }> {
    state = { dolar: false };
    render() {
        let { title, close, codigo, open } = this.props;
        return <Modal show={open} onHide={close} size='xl'>
            <Modal.Header closeButton>
                <h6 className="m-0 font-weight-bold text-primary mr-auto align-self-center">
                    <span><b>{title}</b></span>
                </h6>
            </Modal.Header>
            <Modal.Body>
                <Row>
                    <ReactTooltip />
                    {this.state.dolar ? <Button data-tip="CAMBIAR A BOLIVARES" style={{ marginRight: "15px" }} variant="warning"
                        className="btn-block" onClick={() => { this.setState({ dolar: !this.state.dolar }) }} >
                        <span className="fa fa-usd" /> VISTA EN DOLARES
                </Button> : <Button data-tip="CAMBIAR A DOLARES" style={{ marginRight: "15px" }} variant="primary"
                        className="btn-block" onClick={() => { this.setState({ dolar: !this.state.dolar }) }} >
                        <b>BS</b> VISTA EN BOLIVARES
                </Button>}
                    {this.state.dolar ? <iframe src={"/api/Cotizacion/PDFD/" + codigo} width='100%' height='700wh' title="print" scrolling='no' frameBorder='0' /> :
                        <iframe src={"/api/Cotizacion/PDF/" + codigo} width='100%' height='700wh' title="print" scrolling='no' frameBorder='0' />}
                </Row>
            </Modal.Body>
        </Modal>
    }
}