
import React from 'react'
import { Button } from 'react-bootstrap'
import { Detalles } from '../../../Modelos/General'
import { InputNumber } from '../../../Utiles/Inputs'
import { Producto } from '../../../Modelos/Productos'
import THFila, { THInput } from '../../../Utiles/Componentes/THFila'

interface Props {
    producto: Producto,
    add: (iten: Detalles) => void
}
export default class FilaPrecio extends React.Component<Props, Detalles>{
    constructor(p: Props) {
        super(p)
        this.state = { ...p.producto, precio: p.producto.costo, unidades: 1, comentario: '' };
        this.setState = this.setState.bind(this)
    }
    render() {
        let { precio, descripcion, codigo } = this.state
        return <tr>
            <THFila width='90px'>{codigo}</THFila>
            <THFila width='auto'>{descripcion}</THFila>
            <THInput width='180px'><InputNumber min={.01} step='0.01' setData={this.setState} value={precio} update="precio" /></THInput>
            <THInput width='70px'>
                <Button data-tip="AGREGAR PRODUCTO" onClick={() => { this.props.add(this.state) }} variant="primary">
                    <i className="fa fa-plus" />
                </Button>
            </THInput>
        </tr>
    }

}