import React from 'react'
import { Detalles } from '../../../Modelos/General';
import AuxiliarNuevo, { ProductosModal } from '../../../Utiles/Prototipos/AuxiliarNuevo'
import CalcularImpuesto from '../../../Utiles/Prototipos/CalcularImpuesto'
import FilaDolar from './FilaDolar';
import FilaProducto from './FilaProducto';
import { Cotizacion } from '../../../Modelos/Factura'
import { Button, ButtonGroup, Col } from 'react-bootstrap';
import { ProductosContext, ProductosType } from '../../../Context/Producto';
import { Producto } from '../../../Modelos/Productos';
class AuxiliarCotizacion extends AuxiliarNuevo<{ guardar: () => void }>{
    productos: ProductosModal = 'Cotizacion';
    nuevo = true;
    Row({ data }: { data: Detalles }) {
        let { dolar } = this.props.data as Cotizacion;
        return dolar ? <FilaDolar
            key={'USD' + data.codigo}
            producto={data}
            edit={this.addProductos.bind(this)}
            remove={this.removeProductos.bind(this)} /> :
            <FilaProducto
                key={'BS' + data.codigo}
                producto={data}
                edit={this.addProductos.bind(this)}
                remove={this.removeProductos.bind(this)} />
    }
    BotonesLeft() {
        const { setData, guardar } = this.props;
        return <Col xs={6}>
            <Button onClick={() => { setData({ copiar: true }) }}
                data-class="toolbody" data-html="true"
                data-tip="COPIAR COTIZACION" variant="primary">
                <i className="fa fa-files-o" />
            </Button>
            <ButtonGroup style={{ marginLeft: '15px' }}>
                <Button variant="info" onClick={guardar} data-tip="GUARDAR">
                    <i className="fa fa-floppy-o" />
                </Button>
                <Button variant="warning" onClick={() => { setData({ cargar: true }) }} data-tip="CARGAR">
                    <i className="fa fa-folder-open" />
                </Button>
            </ButtonGroup>
        </Col>
    }
    BotonesRight() {
        const { setData } = this.props;
        let { dolar } = this.props.data as Cotizacion;
        return <Col xs={6} style={{ textAlign: 'right' }}>
            {dolar ? <Button data-tip="CAMBIAR A BOLIVARES" style={{ marginRight: "15px" }}
                variant="warning" onClick={() => setData({ dolar: !dolar })} >
                <span className="fa fa-usd" /> COTIZACION ESTA EN DOLARES
            </Button> : <Button data-tip="CAMBIAR A DOLARES" style={{ marginRight: "15px" }}
                variant="primary" onClick={() => setData({ dolar: !dolar })} >
                <b style={{ fontSize: '10px' }}>BS</b> COTIZACION ESTA EN BOLIVARES
            </Button>}
            <Button data-tip="PROCESAR" style={{ marginRight: "15px" }} variant="success"
                className="float-right" onClick={this.props.Prosesar} >
                <span className="fa fa-print" />
            </Button>
            <ButtonGroup className="float-right" style={{ marginRight: "15px" }}>
                {this.nuevo ? <Button data-tip="NUEVO PRODUCTO" onClick={() => { this.setState({ nuevoProducto: true }) }} variant="info">
                    <i className="fa fa-edit"></i>
                </Button> : <></>}
                <Button data-tip="BUSCAR PRODUCTO" onClick={() => { this.setState({ buscarProducto: true }) }} variant="primary">
                    <span className="fa fa-search"></span>
                </Button>
            </ButtonGroup>
        </Col>
    }
    Calcular({ imponible, exento }: { imponible: number, exento: number, impuesto: number }) {
        let { tasa } = this.context as ProductosType;
        let { dolar } = this.props.data as Cotizacion;
        if (dolar) return <CalcularImpuesto exento={exento / tasa} imponible={imponible / tasa} />;
        return <CalcularImpuesto exento={exento} imponible={imponible} />;
    }
    createProducto(p: Producto) {
        this.addProductos({ ...p, unidades: 1, precio: p.costo * (1 + (1 * p.precio2 / 100)), comentario: "" });
    }
}
AuxiliarCotizacion.contextType = ProductosContext;
export default AuxiliarCotizacion;