import React from 'react'
import { Button, Modal, Row, Col, FormGroup } from 'react-bootstrap'
import { toast } from 'react-toastify'
import { UsuarioFila, conUsuario } from '../../Modelos/Usuario'
import { ControlText } from '../../Utiles/FormControl'
import RolSelector from '../Rol/Selector'
interface Props {
    open: boolean
    user: UsuarioFila | null
    title: string
    close: () => void
    add?: (c: UsuarioFila) => void
}
export default class FormUsuario extends React.Component<Props, UsuarioFila> {
    con = conUsuario();
    constructor(p: Props) {
        super(p)
        this.setState = this.setState.bind(this);
        if (p.user !== null) {
            this.state = p.user
        } else {
            this.state = {
                clave: '',
                codigo: 0,
                nombre: '',
                correo: '',
                rol: '',
                nivel: -1,
                estado: 1
            };
        }
    }

    render() {
        let { setState, state: { clave, nombre, correo, nivel }, props: { open, close, title } } = this
        return <Modal animation show={open} onHide={close}>
            <Modal.Header closeButton>
                <h4 className='mr-auto'>{title}</h4>
            </Modal.Header>
            <Modal.Body>
                <Row>
                    <Col as={FormGroup} xs={12}>
                        <ControlText placeholder="VICTOR REQUENA" before="NOMBRE" maxLength={150}
                            value={nombre} setData={setState} update="nombre" />
                    </Col>
                    <Col as={FormGroup} xs={12}>
                        <ControlText placeholder="VENTAS@AILANTHUS-SISTEMS.COM" before="CORREO" maxLength={150}
                            value={correo} setData={setState} update="correo" />
                    </Col>
                    <Col as={FormGroup} xs={12}>
                        <ControlText placeholder='*********' maxLength={120} type='password'
                            before="CLAVE" value={clave ? clave : ''} setData={setState} update="clave" />
                    </Col>
                    <Col as={FormGroup} xs={12}>
                        <RolSelector defaultValue={nivel} change={(nivel) => { setState({ nivel }) }} />
                    </Col>
                </Row>
            </Modal.Body>
            <Modal.Footer>
                <Button className="float-right" onClick={this.get.bind(this)} variant="primary">
                    <span className="fa fa-floppy-disk" /> Guardar
                </Button>
            </Modal.Footer>
        </Modal>
    }
    get() {
        if (this.state.nombre === "") {
            toast('DEBE COLOCAR EL RASON SOCIAL', { type: toast.TYPE.ERROR })
        } else if (this.state.correo === "") {
            toast('DEBE COLOCAR UN CORREO', { type: toast.TYPE.ERROR })
        } else if (this.state.nivel === -1) {
            toast('DEBE SELECCINAR UN ROL PARA EL USUARIO', { type: toast.TYPE.ERROR })
        } else {
            if (this.props.user === null) {
                if (this.state.clave === "") {
                    toast('DEBE COLOCAR UNA CLAVE', { type: toast.TYPE.ERROR })
                } else {
                    this.con.nuevo(this.state, data => {
                        if (this.props.add)
                            this.props.add(data)
                        this.props.close()
                    })
                }
            } else {
                this.con.actualizar(this.props.user.codigo,
                    this.state, data => {
                        if (this.props.add)
                            this.props.add(data)
                        this.props.close()
                    })
            }
        }
    }

}