import React, { useContext } from 'react'
import { ProductosContext } from '../../Context/Producto';
interface Props { change: (r: number) => void, defaultValue: number }
export default function UnidadSelector(p: Props) {
    const context = useContext(ProductosContext)
    const setRol = (Rol: React.ChangeEvent<HTMLSelectElement>) => { p.change(parseInt(Rol.currentTarget.value)) }
    return <select onChange={setRol} className="form-control" value={p.defaultValue}>
        <option value={-1}>SELECCIONAR UNIDAD</option>
        {(context !== null) ? context.Unidades.map(c => {
            return <option key={"user" + c.codigo} value={c.codigo}>{c.descripcion}</option>
        }) : <></>}
    </select>
}