import React from 'react'
import { ButtonGroup, Button } from 'react-bootstrap'
import ReactTooltip from 'react-tooltip'
import { ProductosContext, ProductosType } from '../../../Context/Producto'
import { Detalles } from '../../../Modelos/General'
import THFila, { THInput } from '../../../Utiles/Componentes/THFila'
import { ControlText } from '../../../Utiles/FormControl'
import { InputNumber } from '../../../Utiles/Inputs'

interface Props {
    producto: Detalles,
    edit: (iten: Detalles) => void,
    remove: (iten: Detalles) => void
}
interface State extends Detalles {
    open: boolean,
}
class FilaDolar extends React.Component<Props, State>{
    constructor(p: Props) {
        super(p)
        this.state = { ...p.producto, open: false }
        this.setState = this.setState.bind(this)
    }
    render() {
        const { tasa, data: { User: { nivel } }, findProducto } = this.context as ProductosType;
        const { state: { unidades, precio, codigo, comentario, open } } = this;
        const { costo, precio1, precio2, precio3, descripcion } = findProducto(codigo);
        let tasaDolar = (tasa !== 0) ? tasa : 1,
            costoDolar = costo / tasaDolar,
            precioDolar = parseFloat((precio / tasaDolar).toFixed(2)),
            precio1Dolar = parseFloat((costoDolar * (1 + precio1 / 100)).toFixed(2)),
            precio2Dolar = parseFloat((costoDolar * (1 + precio2 / 100)).toFixed(2)),
            precio3Dolar = parseFloat((costoDolar * (1 + precio3 / 100)).toFixed(2));
        return <>
            <tr>
                <THFila width='90px'>{codigo}</THFila>
                <THFila width='auto'>{descripcion}</THFila>
                <THInput width='120px'><InputNumber min={.01} step='.01' setData={this.setState} value={unidades} update="unidades" /></THInput>
                <THInput width='180px'>
                    <InputNumber step='0.01' list={codigo + 'list'}
                        min={(nivel === 0) ? parseFloat(costoDolar.toFixed(2)) : precio1Dolar}
                        setData={this.setPrecio.bind(this)} value={precioDolar} update="precio" />
                    <datalist id={codigo + 'list'}>
                        <option>{precio1Dolar}</option>
                        <option>{precio2Dolar}</option>
                        <option>{precio3Dolar}</option>
                    </datalist>
                </THInput>
                <THInput width='230px'><InputNumber disabled setData={() => { }} value={precio * unidades / tasa} update="p" /></THInput>
                <THInput width='110px'>
                    <ButtonGroup>
                        <Button variant="info" onClick={() => { this.setState({ open: !this.state.open }) }} data-tip="DEJAR NOTA">
                            <span className="fa fa-edit"></span>
                        </Button>
                        <ReactTooltip />
                        <Button variant="danger" onClick={() => { this.props.remove(this.state) }} data-tip="QUITAR PRODUCTO">
                            <i className="fa fa-ban"></i>
                        </Button>
                    </ButtonGroup>
                </THInput>
            </tr>
            <tr style={{ display: open ? '' : 'none' }}>
                <td colSpan={7}>
                    <ControlText maxLength={250} before="NOTA INTERNA" setData={this.setState} value={comentario} update="comentario" />
                </td>
            </tr>
        </>
    }
    setPrecio(data: { precio: number }) {
        const { tasa } = this.context as ProductosType;
        this.setState({ precio: data.precio * tasa });
    }
    componentWillUpdate(p: Props, s: State) {
        if (s.precio !== this.state.precio || s.unidades !== this.state.unidades || s.comentario !== this.state.comentario)
            this.props.edit(s)
    }
}
FilaDolar.contextType = ProductosContext;
export default FilaDolar;