import React, { useContext } from 'react';
import { ProductosContext } from '../../Context/Producto';
interface Props { change: (r: string) => void, defaultValue: string, width?: string }
export default function DepartamentoSelector(p: Props) {
    const context = useContext(ProductosContext)
    const setRol = (Rol: React.ChangeEvent<HTMLSelectElement>) => { p.change(Rol.currentTarget.value) }
    return <select onChange={setRol} className="form-control" value={p.defaultValue}>
        <option value={-1}>SELECCIONAR DEPARTAMENTO</option>
        {(context !== null) ? context.Departamentos.map(c => {
            return <option key={"user" + c.codigo} value={c.codigo}>{c.descripcion}</option>
        }) : <></>}
    </select>
}