import React from "react";
import { Card } from "react-bootstrap";
import { toast } from "react-toastify";
import { ProductosContext } from "../../../Context/Producto";
import Ajuste, { AjusteNull, ajusteSend, conAjuste } from "../../../Modelos/Ajuste";
import { InputArea } from "../../../Utiles/Inputs";
import AuxiliarAjuste from "./AuxiliarAjuste";
class NuevaAjuste extends React.Component<any, Ajuste>{
    impuesto = 0;
    con = conAjuste();
    procesar = true;
    constructor(p: any) {
        super(p);
        this.setState = this.setState.bind(this)
        this.state = { ...AjusteNull };
    }
    render() {
        const { state: { nota, codigo }, setState } = this;
        if (codigo > 0)
            return <iframe src={"/api/Ajuste/PDF/" + codigo} width='100%' height='900wh' title="print" scrolling='no' frameBorder='0' />
        return <Card>
            <Card.Header className='bg-info'>
                <h4 className='mr-auto'><i className="fa fa-edit"></i> NUEVO AJUSTE</h4>
            </Card.Header>
            <Card.Body>
                <b>NOTA</b>
                <InputArea rows={4} maxLength={600} value={nota} setData={setState} update="nota" />
                <AuxiliarAjuste key="auxAjuste" Prosesar={this.propcesar.bind(this)} setData={this.setState} data={this.state} />
            </Card.Body>
        </Card>
    }
    propcesar() {
        let procesar = true;
        if (this.state.detalles.length === 0) {
            toast('DEBE SELECCIONAR AL MENOS UN PRODUCTO', { type: toast.TYPE.ERROR });
            procesar = false;
        }
        if (this.state.nota === '') {
            toast('DEBE AGREGAR UNA NOTA', { type: toast.TYPE.ERROR });
            procesar = false;
        }
        if (this.procesar && procesar) {
            this.procesar = false;
        } else {
            toast('YA LE A DADO PROCESAR', { type: toast.TYPE.ERROR });
            procesar = false;
        }
        if (procesar)
            this.con.nuevo(ajusteSend(this.state), data => this.setState({ codigo: data.codigo }));
    }
}
NuevaAjuste.contextType = ProductosContext;
export default NuevaAjuste;