import React from "react";
interface Props {
    width: string | number,
    children: any,
    align?: 'center' | 'right' | 'left'
}
export default function THFila({ width, children, align }: Props) {
    return <th style={{ paddingTop: '1.25rem', minWidth: width, maxWidth: width, width, textAlign: align }}>
        <span>{children} </span>
    </th>
}
export function THInput({ width, children, align }: Props) {
    return <th style={{ minWidth: width, maxWidth: width, width, textAlign: align }}>{children}     </th>
}