import React from 'react'
import { ButtonGroup, Button } from 'react-bootstrap'
import { ProductosContext, ProductosType } from '../../../Context/Producto'
import { Detalles } from '../../../Modelos/General'
import THFila, { THInput } from '../../../Utiles/Componentes/THFila'
import { ControlText } from '../../../Utiles/FormControl'
import { InputNumber } from '../../../Utiles/Inputs'

interface Props {
    producto: Detalles,
    edit: (iten: Detalles) => void,
    remove: (iten: Detalles) => void
}
interface State extends Detalles {
    open: boolean,
}
class FilaFactura extends React.Component<Props, State>{
    constructor(p: Props) {
        super(p)
        console.log(p);
        this.state = { ...p.producto, open: false }
        this.setState = this.setState.bind(this)
    }
    render() {
        const { data: { User: { nivel } }, findProducto } = this.context as ProductosType;
        let { unidades, precio, codigo, comentario, open } = this.state;
        const { costo, precio1, precio2, precio3, descripcion } = findProducto(codigo);
        return <>
            <tr>
                <THFila width='90px'>{codigo}</THFila>
                <THFila width='auto'>{descripcion}</THFila>
                <THInput width='120px'><InputNumber min={1} setData={this.setState} value={unidades} update="unidades" /></THInput>
                <THInput width='180px'>
                    <InputNumber step='0.01' list={costo + 'list'}
                        min={(nivel === 0) ? parseFloat(costo.toFixed(2)) : parseFloat((costo * (1 + precio1 / 100)).toFixed(2))}
                        setData={this.setState} value={precio} update="precio" />
                    <datalist id={costo + 'list'}>
                        <option>{(costo * (1 + precio1 / 100)).toFixed(2)}</option>
                        <option>{(costo * (1 + precio2 / 100)).toFixed(2)}</option>
                        <option>{(costo * (1 + precio3 / 100)).toFixed(2)}</option>
                    </datalist>
                </THInput>
                <THInput width='230px'><InputNumber disabled setData={() => { }} value={precio * unidades} update="p" /></THInput>
                <THInput width='110px'>
                    <ButtonGroup>
                        <Button variant="info" onClick={() => this.setState({ open: !this.state.open })} data-tip="DEJAR NOTA">
                            <span className="fa fa-edit"></span>
                        </Button>
                        <Button variant="danger" onClick={() => this.props.remove(this.state)} data-tip="QUITAR PRODUCTO">
                            <i className="fa fa-ban"></i>
                        </Button>
                    </ButtonGroup>
                </THInput>
            </tr>
            <tr style={{ display: open ? '' : 'none' }}>
                <td colSpan={7}>
                    <ControlText maxLength={250} before="NOTA INTERNA" setData={this.setState} value={comentario} update="comentario" />
                </td>
            </tr>
        </>
    }
    componentWillUpdate(p: Props, s: State) {
        if (s.precio !== this.state.precio || s.unidades !== this.state.unidades || s.comentario !== this.state.comentario)
            this.props.edit(s)
    }
}
FilaFactura.contextType = ProductosContext;
export default FilaFactura;