import React from "react";
import { Accordion, Button, Card } from "react-bootstrap";
import { toast } from "react-toastify";
import { ComprasContext, ComprasType } from "../../Context/Compras";
import { conOrden, Orden, ordenNull, sentOrden } from "../../Modelos/Compra";
import { GeneralFila } from "../../Modelos/General";
import Condiciones from "../../Utiles/Componentes/Condiciones";
import UnAutorization from "../../Utiles/Componentes/UnAutorization";
import AuxiliarCompra from "../Compra/Nuevo/AuxiliarCompra";
import ProveedorSelector from "../proveedores/Selector";
import BuscarOrden from "./BuscarOrden";
interface State extends Orden {
    cargar: boolean
}
class NuevaOrden extends React.Component<any, State>{
    con = conOrden();
    procesar = true;
    constructor(p: any) {
        super(p);
        this.setState = this.setState.bind(this)
        this.state = { ...ordenNull, cargar: false };
    }
    render() {
        if (!this.context.data.Permisos.some((p: string) => p === 'ORDEN'))
            return <UnAutorization />
        let { state } = this;
        if (state.codigo > 0)
            return <iframe src={"/api/Orden/PDF/" + state.codigo} width='100%' height='900wh' title="print" scrolling='no' frameBorder='0' />
        return <Card>
            <Card.Header className='bg-info'>
                <h4 className='mr-auto'><i className="fa fa-edit"></i> NUEVA ORDEN DE COMPRA</h4>
            </Card.Header>
            <Card.Body>
                <ProveedorSelector Proveedor={state.cod_proveedor} add={this.setProveedor.bind(this)} />
                <Accordion>
                    <Accordion.Toggle eventKey='0' as={Button} variant='primary'>DATOS OPCIONALES</Accordion.Toggle>
                    <Accordion.Collapse eventKey='0'><Card>
                        <Card.Body>
                            <Condiciones data={state} setData={this.setState} />
                        </Card.Body>
                    </Card>
                    </Accordion.Collapse>
                </Accordion>
                <BuscarOrden open={state.cargar} add={this.cargar.bind(this)} close={() => { this.setState({ cargar: false }) }} />
                <AuxiliarCompra key="auxOrden" ordenes='COPIAR' Prosesar={this.propcesar.bind(this)} setData={this.setState} data={state} />
            </Card.Body>
        </Card>
    }
    cargar(d: GeneralFila) {
        this.con.get('api/Orden/detalles/' + d.codigo, data => {
            data.codigo = 0;
            this.setState(data);
        })
    }
    setProveedor(p: string) {
        this.setState({ cod_proveedor: p });
    }
    propcesar() {
        let procesar = true;
        const { impuesto } = this.context as ComprasType;
        if (this.state.cod_proveedor === "") {
            toast('DEBE SELECCIONAR UN CLIENTE', { type: toast.TYPE.ERROR });
            procesar = false;
        } else if (this.state.detalles.length === 0) {
            toast('DEBE SELECCIONAR AL MENOS UN PRODUCTO', { type: toast.TYPE.ERROR });
            procesar = false;
        }
        if (this.procesar && procesar) {
            this.procesar = false;
        } else {
            toast('YA LE A DADO PROCESAR', { type: toast.TYPE.ERROR });
            procesar = false;
        }
        if (procesar)
            this.con.nuevo(sentOrden(this.state, impuesto), data => { this.setState({ codigo: data }) });
    }
    componentDidMount() {
        let s = localStorage.getItem('OrdenCache');
        if (s !== null) {
            let data = JSON.parse(s);
            this.setState(data);
        }
    }
    componentDidUpdate() {
        if (!this.state.cargar)
            localStorage.setItem('OrdenCache', JSON.stringify(this.state));
    }
    componentWillUnmount() {
        localStorage.removeItem('OrdenCache');
    }
}
NuevaOrden.contextType = ComprasContext;
export default NuevaOrden;