import React from 'react'
import { Button, FormControl } from 'react-bootstrap'
import { ProductosContext, ProductosType } from '../../../Context/Producto'
import { Detalles } from '../../../Modelos/General'
import { Producto } from '../../../Modelos/Productos'
import THFila, { THInput } from '../../../Utiles/Componentes/THFila'
import { numberFormat } from '../../../Utiles/Formatos'
import { InputNumber } from '../../../Utiles/Inputs'

interface Props {
    producto: Producto,
    add: (iten: Detalles) => void
}
class FilaFactura extends React.Component<Props, Detalles>{
    constructor(p: Props) {
        super(p)
        let { costo, precio2 } = p.producto;
        this.state = {
            ...p.producto,
            unidades: 1,
            comentario: '',
            precio: parseFloat((costo * (1 + (precio2 / 100))).toFixed(2))
        }
        this.setState = this.setState.bind(this);
    }
    render() {
        const { data: { User: { nivel } }, findProducto } = this.context as ProductosType;
        const { unidades, precio, codigo } = this.state;
        const { costo, precio1, precio2, precio3, descripcion, cantidad, inventario } = findProducto(codigo);
        let p = (nivel === 0) ? costo.toFixed(2) : (costo * (1 + precio1 / 100)).toFixed(2);
        return <tr>
            <THFila width='90px'>{codigo}</THFila>
            <THFila width='auto'>{descripcion}</THFila>
            <THInput width='120px'><InputNumber min={.01} step='0.01' setData={this.setState} value={unidades} update="unidades" /> </THInput>
            <THInput width='120px'><FormControl style={{ textAlign: 'right' }} disabled value={(inventario === 1) ? 'NO APLICA' : numberFormat(cantidad)} /> </THInput>
            <THInput width='180px'>
                <InputNumber step='0.01' list={costo + 'list'} setData={this.setState} update="precio"
                    min={parseFloat(p)} value={precio} />
                <datalist id={costo + 'list'}>
                    <option>{(costo * (1 + (precio1 / 100))).toFixed(2)}</option>
                    <option>{(costo * (1 + (precio2 / 100))).toFixed(2)}</option>
                    <option>{(costo * (1 + (precio3 / 100))).toFixed(2)}</option>
                </datalist>
            </THInput>
            <THInput width='230px'><FormControl disabled value={numberFormat((precio * unidades))} /> </THInput>
            <THInput width='70px'>
                <Button data-tip="AGREGAR PRODUCTO" onClick={() => { this.props.add(this.state) }} variant="primary">
                    <i className="fa fa-plus" />
                </Button>
            </THInput>
        </tr>
    }
}
FilaFactura.contextType = ProductosContext;
export default FilaFactura