import React, { useState } from "react";
import { Table } from "react-bootstrap";
import Paginacion from "./Paginacion";
interface Props {
    itens: any[]
    mostrar: number
    row: (d: any) => any
    col: number
    children: any
}
export default function Body(p: Props) {
    const [page, setPage] = useState(1);
    function body() {
        let init = (page - 1) * p.mostrar;
        let fin = init + p.mostrar;
        let itens = p.itens.filter((iten: any, index: number) => {
            if (index >= init && index < fin) {
                return true
            }
            return false
        })
        let pts = itens.map((iten: any) => {
            return p.row(iten);
        })
        return pts
    }
    return <Table striped hover>
        <thead>
            {p.children}
        </thead>
        <tbody>
            {body()}
        </tbody>
        <tr>
            <td colSpan={p.col}>
                <Paginacion page={page}
                    length={p.itens.length}
                    mostrar={p.mostrar}
                    setpage={setPage} />
            </td>
        </tr>
    </Table>
}