import React from 'react'
import { Button, FormControl } from 'react-bootstrap'
import { ProductosContext, ProductosType } from '../../../Context/Producto'
import { Detalles } from '../../../Modelos/General'
import THFila, { THInput } from '../../../Utiles/Componentes/THFila'
import { numberFormat } from '../../../Utiles/Formatos'
import { InputNumber } from '../../../Utiles/Inputs'

interface Props {
    producto: Detalles,
    nota?: number
    edit: (iten: Detalles) => void,
    remove: (iten: Detalles) => void
}
class FilaProducto extends React.Component<Props, Detalles>{
    constructor(p: Props) {
        super(p)
        this.state = { ...p.producto }
        this.setState = this.setState.bind(this)
    }
    render() {
        const { data: { User: { nivel } }, findProducto } = this.context as ProductosType;
        const { state: { unidades, precio, codigo }, props: { nota } } = this;
        const { costo, precio1, precio2, precio3, descripcion, cantidad, inventario } = findProducto(codigo);
        let p = (nivel === 0) ? costo.toFixed(2) : (costo * (1 + precio1 / 100)).toFixed(2);
        let className = '';
        if ((costo * (1 + precio1 / 100) > precio) && (nota === undefined)) className = 'bg-warning';
        if ((cantidad < unidades) && (inventario !== 1) && (nota === undefined)) className = 'bg-danger';
        return <tr className={className}>
            <THFila width='90px'>{codigo}</THFila>
            <THFila width='30%'>{descripcion}</THFila>
            <THInput width='120px'><InputNumber min={.01} setData={this.setState} value={unidades} update="unidades" /></THInput>
            <THInput width='120px'><FormControl style={{ textAlign: 'right' }} disabled value={(inventario === 1) ? 'NO APLICA' : numberFormat(cantidad)} /></THInput>
            <THInput width='180px'>
                <InputNumber step='0.01' setData={this.setState} value={precio} update="precio"
                    list={costo + 'list'} min={parseFloat(p)} />
                <datalist id={costo + 'list'}>
                    <option>{(costo * (1 + precio1 / 100)).toFixed(2)}</option>
                    <option>{(costo * (1 + precio2 / 100)).toFixed(2)}</option>
                    <option>{(costo * (1 + precio3 / 100)).toFixed(2)}</option>
                </datalist>
            </THInput>
            <THInput width='230px'>
                <span style={{ width: '100%', position: 'relative', backgroundColor: "#eee", textAlign: 'right' }}
                    className="form-control">{numberFormat(precio * unidades)}</span>
            </THInput>
            <THInput width='110px'>
                <Button variant="danger" onClick={() => { this.props.remove(this.state) }} data-tip="QUITAR PRODUCTO">
                    <i className="fa fa-ban"></i>
                </Button>
            </THInput>
        </tr>
    }
    componentWillUpdate(p: Props, s: Detalles) {
        if (s.precio !== this.state.precio || s.unidades !== this.state.unidades)
            this.props.edit(s)
    }
}
FilaProducto.contextType = ProductosContext;
export default FilaProducto;