import React from 'react'
import { Button } from 'react-bootstrap'
import { ProductosContext, ProductosType } from '../../../Context/Producto'
import { Detalles } from '../../../Modelos/General'
import { Producto } from '../../../Modelos/Productos'
import THFila, { THInput } from '../../../Utiles/Componentes/THFila'
import { InputNumber } from '../../../Utiles/Inputs'

interface Props {
    producto: Producto,
    add: (iten: Detalles) => void
    tasa: number
}
class FilaDolar extends React.Component<Props, Detalles>{
    constructor(p: Props) {
        super(p)
        let { costo, precio2 } = p.producto;
        this.state = {
            ...p.producto,
            unidades: 1,
            comentario: '',
            precio: parseFloat((costo * (1 + precio2 / 100)).toFixed(2))
        }
        this.setState = this.setState.bind(this)
    }
    render() {
        const { data: { User: { nivel } }, tasa, findProducto } = this.context as ProductosType;
        const { state: { unidades, precio, codigo } } = this;
        const { costo, precio1, precio2, precio3, descripcion } = findProducto(codigo);
        let costoDolar = costo / tasa,
            precioDolar = parseFloat((precio / tasa).toFixed(2)),
            precio1Dolar = parseFloat((costoDolar * (1 + precio1 / 100)).toFixed(2)),
            precio2Dolar = parseFloat((costoDolar * (1 + precio2 / 100)).toFixed(2)),
            precio3Dolar = parseFloat((costoDolar * (1 + precio3 / 100)).toFixed(2));
        let p = (nivel === 0) ? costoDolar.toFixed(2) : (costoDolar * (1 + precio1 / 100)).toFixed(2);
        return <tr>
            <THFila width='90px'>{codigo}</THFila>
            <THFila width='auto'>{descripcion}</THFila>
            <THInput width='120px'><InputNumber min={.01} step='.01' setData={this.setState} value={unidades} update="unidades" /></THInput>
            <THInput width='180px'>
                <InputNumber step='0.01' list={codigo + 'list'} min={parseFloat(p)}
                    setData={this.setPrecio.bind(this)} value={precioDolar} update="precio" />
                <datalist id={codigo + 'list'}>
                    <option>{precio1Dolar}</option>
                    <option>{precio2Dolar}</option>
                    <option>{precio3Dolar}</option>
                </datalist>
            </THInput>
            <THInput width='230px'><InputNumber disabled setData={() => { }} value={(precio / tasa) * unidades} update="p" /></THInput>
            <THInput width='70px'>
                <Button data-tip="AGREGAR PRODUCTO" onClick={() => this.props.add(this.state)} variant="primary">
                    <i className="fa fa-plus" />
                </Button>
            </THInput>
        </tr>
    }
    setPrecio(data: { precio: number }) {
        this.setState({ precio: data.precio * this.context.tasa });
    }
}
FilaDolar.contextType = ProductosContext;
export default FilaDolar;